@media (min-width: 811px) {
  .ProductGallery {
    display: grid;
    grid-template-columns: 150px 1fr;
    grid-gap: 1.8rem;
    max-height: 48rem; } }

.ProductGallery-Additional {
  display: grid;
  grid-gap: 1.8rem; }
  @media (max-width: 810px) {
    .ProductGallery-Additional {
      grid-gap: 2.1rem; } }
  @media (max-width: 810px) {
    .ProductGallery-Additional {
      display: none; } }

.ProductGallery-Slider {
  min-height: 480px;
  height: var(--slider-height); }
  @media (max-width: 810px) {
    .ProductGallery-Slider {
      min-height: 420px; } }
  @media (min-width: 811px) {
    .ProductGallery-Slider::after {
      content: '';
      box-shadow: inset 0 0 0 3px #fff;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      position: absolute;
      pointer-events: none; } }

.ProductGallery .react-transform-component,
.ProductGallery .react-transform-element, .ProductGallery-SliderImage {
  height: 100%;
  width: 100%; }

.ProductGallery-Notice {
  color: var(--secondary-dark-color);
  font-size: 1.08rem;
  text-align: center; }
  @media (max-width: 810px) {
    .ProductGallery-Notice {
      font-size: 1.26rem;
      display: none; } }

.overscrollPrevented {
  overscroll-behavior: contain; }
