:root {
  --product-review-list-border: var(--secondary-dark-color); }

.ProductReviewList {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2.4rem;
  margin-top: 2.4rem; }
  @media (max-width: 810px) {
    .ProductReviewList {
      margin-top: 2.8rem;
      grid-template-columns: 1fr;
      grid-gap: 1.4rem; } }
  .ProductReviewList-Item {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0 0 1.2rem;
    border-bottom: 1px solid var(--product-review-list-border); }
    @media (max-width: 810px) {
      .ProductReviewList-Item {
        padding: 0 0 1.4rem; } }
    @media (min-width: 811px) {
      .ProductReviewList-Item {
        padding: 1.8rem 2.4rem;
        border: 1px solid var(--product-review-list-border); } }
    .ProductReviewList-Item:last-child {
      margin-bottom: 0; }
      @media (max-width: 810px) {
        .ProductReviewList-Item:last-child {
          padding-bottom: 0;
          border-bottom: 0; } }
    .ProductReviewList-Item::before {
      content: none; }
  .ProductReviewList-Title {
    text-align: center;
    font-weight: 700;
    font-size: 2.4rem;
    margin-top: 3.6rem; }
    @media (max-width: 810px) {
      .ProductReviewList-Title {
        font-size: 2.8rem;
        margin-top: 4.2rem; } }
  .ProductReviewList-ReviewTitle {
    width: 100%;
    margin-bottom: 1.2rem; }
    @media (max-width: 810px) {
      .ProductReviewList-ReviewTitle {
        margin-bottom: 1.4rem; } }
  .ProductReviewList-RatingSummary {
    line-height: 1; }
    @media (min-width: 811px) {
      .ProductReviewList-RatingSummary {
        min-width: 180px; } }
    @media (max-width: 810px) {
      .ProductReviewList-RatingSummary {
        margin-bottom: 1.4rem;
        width: 100%; } }
  .ProductReviewList-RatingSummaryItem {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-bottom: .6rem; }
    @media (max-width: 810px) {
      .ProductReviewList-RatingSummaryItem {
        margin-bottom: .7rem; } }
    .ProductReviewList-RatingSummaryItem:last-of-type {
      margin-bottom: 0; }
    .ProductReviewList-RatingSummaryItem .ProductReviewRating {
      margin: 0 0 .24rem 1.2rem; }
      @media (max-width: 810px) {
        .ProductReviewList-RatingSummaryItem .ProductReviewRating {
          margin: 0 0 .28rem 1.4rem; } }
  .ProductReviewList-ReviewContent {
    flex: 1 0 0;
    margin-left: 4.8rem; }
    @media (max-width: 810px) {
      .ProductReviewList-ReviewContent {
        flex: 0 0 auto;
        margin-left: 0;
        width: 100%; } }
    .ProductReviewList-ReviewContent .TextPlaceholder {
      display: block; }
  .ProductReviewList-ReviewAuthor {
    margin-top: .6rem;
    font-size: .96rem; }
    @media (max-width: 810px) {
      .ProductReviewList-ReviewAuthor {
        margin-top: .7rem;
        font-size: 1.12rem; } }
  .ProductReviewList-ReviewDetails {
    margin-bottom: 0; }
    @media (max-width: 810px) {
      .ProductReviewList-ReviewDetails {
        margin-bottom: 1.4rem; } }
    .ProductReviewList-ReviewDetails .TextPlaceholder {
      margin-top: .6rem;
      line-height: 1.1; }
      @media (max-width: 810px) {
        .ProductReviewList-ReviewDetails .TextPlaceholder {
          margin-top: .7rem; } }
      .ProductReviewList-ReviewDetails .TextPlaceholder::after {
        display: block; }
